.image-container {
    border: none;
    /* border-radius: 8px; */
    overflow: hidden;
    /* width: calc(20% - 20px); */
    margin: 10px;
    text-align: center;
    height: 100%;
    position: relative;
}


/* .image-oncard {
    position: relative;
} */

.image-text {
    position: absolute;
    /* bottom: 133px; */
    top: 75%;
    text-align: left;
    width: -webkit-fill-available;
}

.image-container img {
    display: block;
}

.image-oncard {
    user-select: none;
    /* Disable text selection */
    -webkit-user-drag: none
}

.character-name {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    color: white;
    background-color: rgba(0, 0, 0, 0.7);
    text-align: center;
    padding: 5px 0;
    margin: 0;
}

.character-info {
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.info-left p {
    text-align: left;
    color: #ffffff;
}

.info-right p {
    margin: 0;
    text-align: right;
    color: #ff9800;
}

.info-left p::after,
.info-right p::after {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-color: #ddd;
    margin-top: 5px;
}

.info {
    display: flex;
    justify-content: space-between;
    background-color: #323131;
}